import * as React from 'react';

import { Navigation } from './core';
import { Shell } from './layout';

import pages from './pages';

class App extends React.Component<{}, {}> {
  render() {
    return (
      <Navigation
        pages={pages}
      >
        <Shell />
      </Navigation>
    );
  }
}

export default App;



