import * as React from 'react';

import { Page } from 'core';
import './Header.css';

export interface HeaderItem extends Page {
  label?: string;
}

export interface HeaderProps {
  className?: string;
  items?: HeaderItem[];
}

export interface HeaderState {
  isVisible: boolean;
}

export class Header extends React.Component<HeaderProps, HeaderState> {
  state = {
    isVisible: false,
  };

  public render(): JSX.Element {
    const { items } = this.props;
    const { isVisible } = this.state;
    const className = isVisible ? 'open' : 'closed';

    return (
      <div className={`header ${className || ''}`}>
        <i className="fa fa-bars menuButton" onClick={this._toggleVisible} />
        <h1>Site Name</h1>
        <div>
          {
            items && items.map((item) => (
              <a key={item.route} href={`/#${item.route}`} onClick={this._toggleVisible}>{item.label}</a>
            ))
          }
        </div>
      </div>
    );
  }

  private _toggleVisible = () => {
    const { isVisible } = this.state;
    this.setState({ isVisible: !isVisible });
  }
}



