import * as React from 'react';

import { Grid, Col, PageWrapperProps } from '../../core';

import './About.css';

export class About extends React.Component<PageWrapperProps, {}> {
  public render(): JSX.Element {
    const { className } = this.props;
    return (
      <div className={className}>
        <div className="parallax">
          <div className="bg__me" />
          <Grid>
            <Col className="l4 show-l">
              <div className="bg__hand_right" />
            </Col>
            <Col className="l4 m12 center">
              <h1 className="font-callout">Jacob Ebey</h1>
              <h4 className="font-sPlus">SOFTWARE ENGINEER | REDMOND, WA</h4>
            </Col>
            <Col className="l4 show-l">
              <div className="bg__hand_left" />
            </Col>
          </Grid>
        </div>
        <div className="content parallax-content">
          <Grid>
            <Col className="m9">
              <p>
                Although a software engineer by day, I am never one to shy away from a mechanical or
                electrical issue and have spent many long hours in the robotics lab. As a former Regional&nbsp;
                <a href="https://www.firstinspires.org/">FIRST</a> Dean's List winner, I continue to try
                to inspire not just the youth, co-workers and community.
              </p>
              <p>
                Born and raised around the greater Seattle area, I am currently living in Redmond as work
                has landed me at Microsoft. You will occasionally catch me at a local hangout, J Michael's,
                working with local students to further their skills and understanding.
              </p>
            </Col>
            <Col className="m3 iconLinks center">
              <a href="/#/resume">
                <i className="fa fa-file-text-o" />
                <br />
                Resume
              </a>
              <a href="http://github.com/jacob-ebey">
                <i className="fa fa-github" />
                <br />
                GitHub
              </a>
            </Col>
          </Grid>
          <Grid>
            <Col className="m3">
              <p>jacob.ebey@live.com</p>
              <p>206.578.7230</p>
              <p>Redmond, WA, USA</p>
            </Col>
            <Col className="m9">
              Contact Form Goes Here...
            </Col>
          </Grid>
        </div>
      </div>
    );
  }
}



