import * as React from 'react';

import MarkdownRenderer from 'react-markdown-renderer';

import { PageWrapperProps } from '../../core';

interface BlogEntryState {
  markdown: string;
}

export class BlogEntry extends React.Component<PageWrapperProps, BlogEntryState> {
  state = {
    markdown: '',
  };

  public componentDidMount() {
    fetch('https://raw.githubusercontent.com/jacob-ebey/blog-pages/master/TestEntry.md', {
      method: 'get'
    }).then((response) => {
      return response.text();
    }).then((markdown) => {
      this.setState({ markdown });
    });
  }

  public render(): JSX.Element {
    const { className } = this.props;
    const { markdown } = this.state;

    return (
      <div className={className}>
        <div className="content">
          <MarkdownRenderer markdown={markdown} />
        </div>
      </div>
    );
  }
}



