import * as React from 'react';

// tslint:disable-next-line: import-name
import Router from '@funjs/route-parser';

import { NavigationProps, NavigationState, Page } from './Navigation.Props';

const initialPage: Page | undefined = undefined;
export class Navigation extends React.Component<NavigationProps, NavigationState> {
  state = {
    activePage: initialPage,
    // tslint:disable-next-line no-any
    match: undefined,
  };

  public componentDidMount() {
    this._onHashChange(window.location.href);

    window.addEventListener('hashchange', this._eventListener, false);
  }

  public componentWillUnmount() {
    window.removeEventListener('hashchange', this._eventListener, false);
  }

  public render(): JSX.Element {
    const { children, pages } = this.props;
    const { activePage, match } = this.state;

    const currentElement = activePage ? React.createElement(activePage.component) : undefined;

    return (
      <div className="navigation-wrapper">
        {
          // tslint:disable-next-line: no-any
          React.Children.map(children, (child: React.ReactElement<any>) =>
            React.cloneElement(child, {
              currentElement,
              activePage,
              match,
              pages,
            })
          )
        }
      </div>
    );
  }

  private _eventListener: EventListener = (event: HashChangeEvent) => {
    this._onHashChange(event.newURL);
  }

  private _onHashChange = (newUrl: string | null) => {
    const { pages } = this.props;
    const url = newUrl ? newUrl.split('#', 2)[1] : undefined;

    if (pages) {
      let match;
      let activePage;
      if (url && url !== '/') {
        activePage = pages.find((page: Page) => {
          const matchResult = Router(page.route).match(url);

          if (matchResult === false) {
            return false;
          }

          match = matchResult;
          return true;
        });
        if (!activePage) {
          activePage = pages.find((page) => page.is404 === true);
        }
      } else {
        activePage = pages.find((page) => page.isIndex === true);
      }

      window.scrollTo(0, 0);
      this.setState({ activePage, match });
    }
  }
}



