import { HeaderItem, Page } from '../core';

import { About } from './About';
import { Blog, BlogEntry } from './Blog';
import { Resume } from './Resume';

type CombinedPage = Page | HeaderItem;

const pages: CombinedPage[] = [
  {
    label: 'Blog',
    route: '/blog',
    component: Blog,
    isIndex: true,
  },
  {
    route: '/blog/:id',
    component: BlogEntry,
  },
  {
    label: 'About',
    route: '/about',
    component: About,
  },
  {
    label: 'Resume',
    route: '/resume',
    component: Resume,
    is404: true,
  },
];

export default pages;



