import * as React from 'react';

import { Header, PageWrapperProps } from '../../core';

import './Shell.css';

export class Shell extends React.Component<PageWrapperProps, {}> {
  public render(): JSX.Element {
    const { activePage, currentElement, match, pages } = this.props;
    return (
      <div className="shell">
        <Header className="header" items={pages && pages.filter((page) => 'label' in page)} />
        {
          currentElement && React.cloneElement(currentElement, {
            className: 'body',
            activePage,
            currentElement,
            match,
          })
        }
        <div className="footer center">&#169; Jacob Ebey 2017</div>
      </div>
    );
  }
}



